import React, { Component } from "react";
import { Col, Row } from "reactstrap";
import Select from 'react-select';
import { FilePond } from 'react-filepond';
import 'filepond/dist/filepond.min.css';

import "./uploadImages.css";

const options = [
    { value: 'person', label: 'Person' },
    { value: 'tree', label: 'Tree' },
    { value: 'animal', label: 'Animal' }
  ];

class UploadImages extends Component {
    state = {
        selectedOption: null,
    }

    handleChange = (selectedOption) => {
        this.setState({ selectedOption });
        console.log(`Option selected:`, selectedOption);
    }

    render() {
        const { selectedOption } = this.state;

        return (
        <div className="animated fadeIn container container">
            <Row>
            <Col xs="12">
                <div className="d-flex justify-content-center mb-3 mt-3">
                <div className="p-2 AI-photo-head-container pb-2">
                    <span className="AI-photo-head"> Upload Dataset </span>
                </div>
                </div>
            </Col>
            </Row>
            <div className="row">
                <div className="col">
                    <div className="row d-flex justify-content-center">
                    <span className="col-3"></span>
                    <Select className="col-7"
                        value={selectedOption}
                        onChange={this.handleChange}
                        options={options}
                    />
                    <span className="col-2"></span>
                    </div>
                </div>
            </div>
            <div className="row" style={{"marginTop": "30px"}}>
                <div className="col"></div>
                <div className="col">
                <FilePond allowMultiple={true}/>

                    {/* <button className="btn btn-primary btn-lg">Upload Dataset</button> */}
                </div>
                <div className="col"></div>
            </div>
        </div>
        );
    }
}

export default UploadImages;
